import React from 'react';
import '../styles/TileSet.css';
import Developer from './Developer';
import Professional from './Professional';
import Project from './Project';
import laptopIcon from '../img/laptop-code-solid.svg';
import laptopIconWhite from '../img/laptop-code-solid-white.svg';
import proDudeIcon from '../img/user-tie-solid.svg';
import proDudeIconWhite from '../img/user-tie-solid-white.svg';
import projectIcon from '../img/project-diagram-solid.svg';
import projectIconWhite from '../img/project-diagram-solid-white.svg';

class TileSet extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeTile: ""
    };
  }

  switchActiveIcon = (iconStr) => {
    if (iconStr === this.state.activeTile) {
      this.setState({ activeTile: "" });
    } else {
      this.setState({ activeTile: iconStr });
    }
  }

  getIcon = (iconStr) => {
    const icons = {
      "laptop": laptopIcon,
      "laptop-active": laptopIconWhite,
      "proDude": proDudeIcon,
      "proDude-active": proDudeIconWhite,
      "project": projectIcon,
      "project-active": projectIconWhite,
    };

    return icons[iconStr];
  }

  getTileImg = (tile) => {
    if (this.state.activeTile === tile) {
      return this.getIcon(tile + "-active");
    } else {
      return this.getIcon(tile);
    }
  }

  handleActiveSection = (icon) => {
    if (this.state.activeTile === icon) {
      return "";
    } else {
      return "hidden";
    }
  }

  render() {
    return (
      <div id="content">
        <div className="row">
          <div className="tile col-4">
          <img className="laptop tile-icon" 
            src={this.getTileImg("laptop")} 
            alt="laptop" 
            onClick={() => this.switchActiveIcon("laptop")}/>
            <br/>
            <span>Web Developer</span>
          </div>

          <div className="tile col-4">
            <img className="pro-dude tile-icon" 
            src={this.getTileImg("proDude")} 
            alt="proDude" 
            onClick={() => this.switchActiveIcon("proDude")} />
            <br/>
            <span>Professional</span>
          </div>

          <div className="tile col-4">
            <img className="project tile-icon" 
            src={this.getTileImg("project")} 
            alt="project" 
            onClick={() => this.switchActiveIcon("project")} />
            <br/>
            <span>Problem Solver</span>
          </div>
        </div>

        <div className={this.handleActiveSection("laptop")}><Developer /></div>
        <div className={this.handleActiveSection("proDude")}><Professional /></div>
        <div className={this.handleActiveSection("project")}><Project /></div>

      </div>
    );
  }
}

export default TileSet;