import React from 'react';
import '../styles/Professional.css';
import caretDown from '../img/caret-down-solid.svg';
import caretUp from '../img/caret-up-solid.svg';
import linkedInIcon from '../img/linkedin-brands.svg';
import twitterIcon from '../img/twitter-square-brands.svg';
import envIcon from '../img/envelope-solid.svg';
import githubSqIcon from '../img/github-square-brands.svg';

class Professional extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      "careerOpen": false,
      "leaderOpen": false,
      "learnerOpen": false
    };
  }

  toggleCareerChange = () => {
    if (this.state.careerOpen === false) {
      this.setState({ careerOpen: true });
    } else {
      this.setState({ careerOpen: false });
    }
  }

  careerChangeClass = () => {
    if (this.state.careerOpen === true) {
      return "";
    } else {
      return "hidden";
    }
  }

  careerChangeCaretImg = () => {
    if (this.state.careerOpen === true) {
      return caretUp;
    } else {
      return caretDown;
    }
  }

  toggleLeader = () => {
    if (this.state.leaderOpen === false) {
      this.setState({ leaderOpen: true });
    } else {
      this.setState({ leaderOpen: false });
    }
  }

  leaderClass = () => {
    if (this.state.leaderOpen === true) {
      return "";
    } else {
      return "hidden";
    }
  }

  leaderChangeCaretImg = () => {
    if (this.state.leaderOpen === true) {
      return caretUp;
    } else {
      return caretDown;
    }
  }

  toggleLearner = () => {
    if (this.state.learnerOpen === false) {
      this.setState({ learnerOpen: true });
    } else {
      this.setState({ learnerOpen: false });
    }
  }

  learnerClass = () => {
    if (this.state.learnerOpen === true) {
      return "";
    } else {
      return "hidden";
    }
  }

  learnerChangeCaretImg = () => {
    if (this.state.learnerOpen === true) {
      return caretUp;
    } else {
      return caretDown;
    }
  }

  render() {

    return(
      <div className="professional-wrapper" id="professional">
        <div className="row">
          <div className="col-6 identity">
            <div className="professional-heading">
              <h3>Identity</h3>
            </div>
            
            <div className="row">
              <div className="col-11 offset-1">
                <h5 className="h5-careted">Career Changer</h5>
                <img className="caret-icon" 
                  src={this.careerChangeCaretImg()} 
                  alt="caret" 
                  onClick={this.toggleCareerChange} />
                <div className={this.careerChangeClass()}>
                <p>Before my journey into full-stack web development, I was a P.E. teacher who moonlighted as a coach, Bingo host and KJ.</p>
                <p>Managing people from school-age children to Sinatra-loving power dabbers taught me patience, empathy, and fairness.</p>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-11 offset-1">
                <h5 className="h5-careted">Leader</h5>
                <img className="caret-icon" 
                  src={this.leaderChangeCaretImg()} 
                  alt="caret" 
                  onClick={this.toggleLeader} />
                <div className={this.leaderClass()}>
                <p>The leadership abilities I gained from teaching have had an extremely positive effect during my time in web development.</p>
                <p>As a consultant I've been a voice of wisdom and reason for my peers, and as a developer I always welcome feedback to consistently push for self-improvement.</p>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-11 offset-1">
                <h5 className="h5-careted">Lifelong Learner</h5>
                <img className="caret-icon" 
                  src={this.learnerChangeCaretImg()} 
                  alt="caret" 
                  onClick={this.toggleLearner} />
                <div className={this.learnerClass()}>
                <p>Forever a student of technology, I am constantly building on my skill set to fit business needs.</p>
                <p>Aside from frequently picking up languages and frameworks, my communication skills and attention to detail grow daily.</p>
                </div>
              </div>
            </div>


          </div>



          <div className="col-6 qualities contact">
            <div className="professional-heading">
              <h3>Qualities</h3>
            </div>

            <div className="row">
              <div className="col-11 offset-1">
                <ul>
                  <li>Detail-oriented</li>
                  <li>Communicator</li>
                  <li>Fast learner</li>
                  <li>Positive</li>
                  <li>Energetic</li>
                  <li>Loyal</li>
                </ul>
              </div>
            </div>

            <div className="professional-heading">
              <h3>Contact</h3>
            </div>

            <div className="row">
              <div className="col-12 contact-icon-col">
                <img className="contact-icon" src={envIcon} alt="" />
                <p className="contact-email-text">tladislas@gmail.com</p>
              </div>
            </div>

            <div className="row">
              <div className="col-4 contact-icon-col">
                <a href="https://www.linkedin.com/in/toby-ladislas/" target="_blank" rel="noopener noreferrer">
                  <img className="contact-icon" src={linkedInIcon} alt="" />
                </a>
                <p className="contact-icon-text">LinkedIn</p>
              </div>

              <div className="col-4 contact-icon-col">
                <a href="https://github.com/tobylad" target="_blank" rel="noopener noreferrer">
                  <img className="contact-icon" src={githubSqIcon} alt="" />
                </a>
                <p className="contact-icon-text">Github</p>
              </div>

              <div className="col-4 contact-icon-col">
                <a href="https://twitter.com/ladislast" target="_blank" rel="noopener noreferrer">
                  <img className="contact-icon" src={twitterIcon} alt="" />
                </a>
                <p className="contact-icon-text">Twitter</p>
              </div>
            </div>

          </div>


        </div>
      </div>
    );
  }
}

export default Professional;

